import React from "react";
import { Pane, Image, Link } from 'evergreen-ui';


// Custom components
// import Badge from './Badge';
import badges from './BadgesStore';

const Badge = ({link, image, alt}) => (
    link ? <Link height="1.25em" style={{marginRight: 8}} as="a" href={link} ><Image src={image} alt={alt} /></Link> : <br />
)

export default () => (
    <Pane margin={4}>{badges.map(badge => <Badge key={badge.alt} {...badge} />)}</Pane>
)
