import React from "react";
import { Button } from 'evergreen-ui';

import { InlineIcon } from '@iconify/react';

import { links } from './LinksStore';


const HomePaneLink = ({ icon, color, title, url: href, style, size = "1.25em" }) => (
  <Button is="a" target="blank" appearance="minimal" height={32} intent="none" margin={4} key={title} {...{href, style}}>
    <InlineIcon width={size} height={size} {...{icon, color}}>
      {title}
    </InlineIcon>
  </Button>
)

