const LinksPane = () => (
    <div>
      {links.map(link => <HomePaneLink key={link.title} {...link} />)}
      <br />
      {/* {buttons.map(button => <HomePaneButton key={button.title} {...button} />)} */}
    </div>
)

export default LinksPane;
